.category .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
}

.category .container .banner {
  width: 100%;
}

.category .container .middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: var(--paragraph-color);
}

.category .container .category-products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 20px;
  row-gap: 40px;
}

.category .container .category-products .item .item-image {
  width: 100%;
}

.category .container .catgory-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 70px 0 120px 0;
}

.category .container .catgory-bottom button {
  border: none;
  padding: 15px 30px;
  border-radius: 40px;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  -ms-border-radius: 40px;
  -o-border-radius: 40px;
  color: rgb(130, 127, 127);
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;
}

.category .container .middle span {
  font-weight: bold;
}
