.description-reviews {
  margin-top: 100px;
}

.description-reviews .toggle-review-desc {
  display: flex;
}

.description-reviews .toggle-review-desc div {
  border: 1px solid #d0d0d0;
  padding: 10px 15px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
}

.description-reviews .toggle-review-desc .active {
  background: #d0cdcd;
}

.desc-reviews-content {
  border: 1px solid #d0d0d0;
  padding: 25px;
}
